.form {
    box-sizing: border-box;
    background-color: white;
    width: 430px;
    border-radius: 10px;
    padding: 34px 36px 37px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
}

@media (max-width: 600px) {
    .form {
        width: 282px;
        padding: 25px 22px;
    }
}